<script>
import Navbar from "@/components/Navbar.vue";
import ToastList from "@/components/ToastList.vue";
import { ref, onMounted } from "vue";
import axios from "axios"; // 替代 this.$http
import Footer from "@/components/Footer.vue";
export default {
  components: {
    Navbar,
    ToastList,
    Footer,
  },
  data() {
    return {
      activeIndex: 0,
      isLoading: false,
      products: [],
      categories: [],
      selectListCategory: [],
    };
  },
  methods: {
    addCart(item) {
      const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/cart`;
      this.$http.post(api, { data: { product_id: item.id, qty: 1 } }).then((res) => {
        console.log(res);
        if (res.data.success) {
          this.$root.emitter.emit("push-message", {
            style: "success",
            message: "已加入購物車",
            content: "",
          });
        }
      });
    },
    isActive(category) {
      return this.$route.path === category.path;
    },
    filterItemList(products, categoryName) {
      return products.filter((item) => {
        return item.category === categoryName;
      }).length;
    },
    changeCategory(index) {
      this.activeIndex = index;
      this.selectListCategory =
        this.activeIndex === 0
          ? this.products
          : this.products.filter((item) => item.category === this.categories[index].zh);
    },
  },
  created() {
    const api = `${process.env.VUE_APP_API_PATH}/api/${process.env.VUE_APP_API_NAME}/products`;
    this.isLoading = true;
    this.$http.get(api).then((res) => {
      console.log(res);
      if (res.data.success) {
        const products = res.data.products;
        this.products = products;
        this.isLoading = false;

        this.categories = [
          { zh: `全部商品`, en: `All`, number: products.length },
          {
            zh: `飛行眼鏡`,
            en: `Aviator Sunglasses`,
            number: this.filterItemList(products, "飛行眼鏡"),
          },
          {
            zh: `方形眼鏡`,
            en: `Square Sunglasses`,
            number: this.filterItemList(products, "方形眼鏡"),
          },
          {
            zh: `圓形眼鏡`,
            en: `Round Sunglasses`,
            number: this.filterItemList(products, "圓形眼鏡"),
          },
          {
            zh: `偏光眼鏡`,
            en: `Polarized Sunglasses`,
            number: this.filterItemList(products, "偏光眼鏡"),
          },
          {
            zh: `造型眼鏡`,
            en: `Styling Sunglasses`,
            number: this.filterItemList(products, "造型眼鏡"),
          },
        ];
        this.selectListCategory = this.products;
      }
    });
  },
};
</script>
